import blog from '../../../src/api/blog'
import auth from '../../../src/api/auth'

window.auth = auth
window.blog = blog

export default {
  name: 'HelloWorld',
  data () {
    return {
      userId: null,
      page: null,
      total: null,
      totalPage: '',
      blogs: []
    }
  },
  created(){
    this.page = parseInt(this.$route.query.page) || 1
    this.userId = this.$route.params.userId
    blog.getBlogsByUserId(this.userId, {page: this.page}).then(res=>{
      let {page, total, totalPage, data} = res
      this.page = page
      this.total = total
      this.totalPage = totalPage
      this.blogs = data
      console.log(this.blogs,1111111);
    })
  },
  mounted(){
    console.log(this.splitDate('2018-09-14T09:57:30.984Z'));
  },
  methods: {
    splitDate(dateStr){
      let date = typeof dateStr === 'object' ? dateStr : new Date(dateStr)
      let day = date.getDate()
      let month = date.getMonth() + 1
      let year = date.getFullYear()
      return {day,month,year}
    },
    onPageChange(newPage){
      blog.getBlogsByUserId(this.userId,{page: newPage}).then(res=>{
        console.log(res);
        this.page = res.page
        this.total = res.total
        this.totalPage = res.totalPage
        this.blogs = res.data
        console.log(this.blogs);
        this.$router.push({path: `/user/${this.userId}`, query: {page: newPage}})
      })
    },
    randomPicNum(){
      let num = 1 + Math.round(Math.random()*12)
      return num
    }
  }
}



